import AppScore from './AppScore.vue';
import AppLevelBadge from './AppLevelBadge.vue';
import AppManagement from '../../mixins/AppManagement.js';
import SvgFilterMixin from '../SvgFilterMixin.vue';
import NcButton from '@nextcloud/vue/dist/Components/NcButton.js';
export default {
  name: 'AppItem',
  components: {
    AppLevelBadge,
    AppScore,
    NcButton
  },
  mixins: [AppManagement, SvgFilterMixin],
  props: {
    app: {
      type: Object,
      required: true
    },
    category: {
      type: String,
      required: true
    },
    listView: {
      type: Boolean,
      default: true
    },
    useBundleView: {
      type: Boolean,
      default: false
    },
    headers: {
      type: String,
      default: null
    },
    inline: {
      type: Boolean,
      default: false
    }
  },
  data() {
    return {
      isSelected: false,
      scrolled: false,
      screenshotLoaded: false
    };
  },
  computed: {
    hasRating() {
      return this.app.appstoreData && this.app.appstoreData.ratingNumOverall > 5;
    },
    dataItemTag() {
      return this.listView ? 'td' : 'div';
    },
    withSidebar() {
      return !!this.$route.params.id;
    }
  },
  watch: {
    '$route.params.id'(id) {
      this.isSelected = this.app.id === id;
    }
  },
  mounted() {
    this.isSelected = this.app.id === this.$route.params.id;
    if (this.app.releases && this.app.screenshot) {
      const image = new Image();
      image.onload = () => {
        this.screenshotLoaded = true;
      };
      image.src = this.app.screenshot;
    }
  },
  watchers: {},
  methods: {
    prefix(prefix, content) {
      return prefix + '_' + content;
    },
    getDataItemHeaders(columnName) {
      return this.useBundleView ? [this.headers, columnName].join(' ') : null;
    }
  }
};