import NcIconSvgWrapper from '@nextcloud/vue/dist/Components/NcIconSvgWrapper.js';
import { mdiStar, mdiStarHalfFull, mdiStarOutline } from '@mdi/js';
import { translate as t } from '@nextcloud/l10n';
import { defineComponent } from 'vue';
export default defineComponent({
  name: 'AppScore',
  components: {
    NcIconSvgWrapper
  },
  props: {
    score: {
      type: Number,
      required: true
    }
  },
  setup() {
    return {
      mdiStar,
      mdiStarHalfFull,
      mdiStarOutline
    };
  },
  computed: {
    title() {
      const appScore = (this.score * 5).toFixed(1);
      return t('settings', 'Community rating: {score}/5', {
        score: appScore
      });
    },
    fullStars() {
      return Math.floor(this.score * 5 + 0.25);
    },
    emptyStars() {
      return Math.min(Math.floor((1 - this.score) * 5 + 0.25), 5 - this.fullStars);
    },
    hasHalfStar() {
      return this.fullStars + this.emptyStars < 5;
    }
  }
});